import { Component, h, Element, Event, EventEmitter, State } from '@stencil/core';
import tailwind from '../../output.css';
import { languages } from '../../languages';
import { currencies } from '../../currencies';
@Component({
  tag: 'rf-popup',
  shadow: true,
})
export class Popup {
  @Element() element: HTMLElement;
  componentDidLoad() {
    const shadowRoot = this.element.shadowRoot;
    if (shadowRoot) {
      const style = document.createElement('style');
      style.textContent = tailwind;
      shadowRoot.appendChild(style);
    }
  }

  languages = languages;
  currencies = currencies;

  @Event({ bubbles: true, composed: true }) rf_hide_popup: EventEmitter<boolean>;
  hide() {
    this.rf_hide_popup.emit(false);
  }

  @State() language = 'English';
  @State() currency =  'US$';

  @State() index: number;
  getLang(lang, i) {
    this.language = lang;
    this.index = i;
  }

  getCur(event: Event) {
    this.currency = (event.target as HTMLSelectElement).value;
  }

  @Event({ bubbles: true, composed: true }) rf_send_data: EventEmitter<{ lang: string; cur: string }>;
  send() {
    this.rf_send_data.emit({ lang: this.language, cur: this.currency });
  }

  render() {
    return (
      <div class="fixed inset-0 flex justify-center items-center z-50">
        <div class="absolute inset-0 bg-gray-800 opacity-75"></div>
        <div class="bg-white z-10 w-full md:w-3/4 lg:w-1/2 xl:w-1/3 flex flex-col gap-8 p-6 rounded-lg">
          <div class="flex flex-row justify-between">
            <h1 class="text-2xl font-semibold">Display Settings</h1>
            <button class="rounded-full hover:bg-gray-200 text-gray-500 font-semibold px-2" onClick={this.hide.bind(this)}>
              X
            </button>
          </div>
          <div class="grid grid-cols-4 gap-2">
            {this.languages.map((l, i) => {
              return (
                <div
                  class={{
                    'flex': true,
                    'flex-row': true,
                    'gap-1': true,
                    'hover:cursor-pointer': true,
                    'hover:bg-red-200': true,
                    'rounded-md': true,
                    'p-1': true,
                    'bg-red-200': this.index == i ? true : false,
                  }}
                  onClick={this.getLang.bind(this, l.description, i)}
                >
                  <div class="rounded-full overflow-hidden w-5 h-5">
                    <img src={l.flag} class="object-cover w-5 h-5"></img>
                  </div>
                  <div class="text-xs">{l.description}</div>
                </div>
              );
            })}
          </div>
          <div class="relative w-full">
            <select class="border-2 rounded-md bg-white border-gray-400 h-10 w-full outline-none px-3" onChange={this.getCur.bind(this)}>
              {this.currencies.map(c => {
                return <option value={c.symbol} selected={c.symbol === this.currency}>{c.symbol}</option>;
              })}
            </select>
            <label class="absolute top-0 left-4 text-gray-400 -translate-y-3 bg-white px-1">Currency</label>
          </div>
          <div class="flex flex-row justify-end gap-4">
            <button class="border-2 border-gray-400 text-gray-400 rounded-full px-2 py-1" onClick={this.hide.bind(this)}>
              Cancel
            </button>
            <button
              class="border-2 border-red-800 text-white bg-red-800 rounded-full px-2 py-1"
              onClick={() => {
                this.send();
                this.hide();
              }}
            >
              Confirm
            </button>
          </div>
        </div>
      </div>
    );
  }
}
